<template>
  <div class="app-container">
    <div class="p-5 relative min-h-screen">
      <div class="flex items-center justify-center">
        <span class="font-semibold text-2xl tracking-wide capitalize">{{name}}</span>
      </div>

      <div class="flexjustify-center items-center absolute left-0 bottom-0 p-5 w-full">
        <button
          @click="logout()"
          class="flex mx-auto border border-pink-600 justify-center p-3 px-6 w-auto"
        >Sign Out</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "Home",
  data() {
    return {
      loading: ""
    };
  },
  computed: {
    ...mapGetters(["name"])
  },
  methods: {
    async logout() {
      await this.$store.dispatch("user/logout");
      this.$router.push(`/login?redirect=${this.$route.fullPath}`);
    }
  }
};
</script>

